.assetsRoot {
  display: flex;
  flex-direction: column;
  gap: 17px;
  width: 100%;
}
.title {
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  color: #ffffff;
}
.assetsInfo {
  background: #181822;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 15px;

  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;

  color: #ffffff;

  display: flex;
  flex-direction: column;

  gap: 14px;
}
.assetsInfoTotal {
  display: flex;
  gap: 15px;
}
.assetsInfoCoin {
  display: flex;
  align-items: center;
  gap: 11px;
  img {
    width: 27px;
    height: 27px;
  }
}
.total {
  font-weight: 500;
}
.loading {
  display: flex;
  justify-content: center;
  align-items: center;

  background: #181822;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  min-height: 94px;
}
