.root {
  height: 40px;
  display: flex;
  gap: 10px;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  @media (max-width: 600px) {
    gap: 4px;
    height: 35px;
  }
}
.button {
  padding: 0 14px;

  font-family: "Gilroy";
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 1.2px;

  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;

  color: #ffffff;

  background: #2f2f38;
  border-radius: 10px;

  cursor: pointer;
  transition: background 0.2s linear;
  &:hover {
    background: rgba(255, 255, 255, 0.4);
  }

  @media (max-width: 600px) {
    font-size: 14px;
    line-height: 16px;
    padding: 0 10px;
  }
}
