.root {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
  width: 100%;

  & > label:first-child {
    input {
      padding-right: 134px;

      @media (max-width: 400px) {
        padding-right: 110px;
      }
    }
  }
}
.transferButton {
  width: fit-content;
  min-width: 300px;
  margin-top: 20px;
  height: 63px;
  padding: 16px 75px;
  background: #4734e9;
  border-radius: 10px;
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;

  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;

  color: #ffffff;
  cursor: pointer;

  @media (max-width: 600px) {
    font-size: 18px;
    line-height: 28px;
    padding: 16px 35px;
  }
}
