$leftPadding: 15px;

.root {
  margin: 0;
  padding: 0;
  background: #181822;
  border: 2px solid #ffffff10;
  border-radius: 10px;

  height: 72px;
  overflow: hidden;
}
.input {
  margin: 0;

  input {
    background: transparent;
    height: 100%;
    width: 100%;
    margin: 0;
    padding-top: 40px;
    padding-left: $leftPadding;

    outline: none;

    font-family: "Gilroy";
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 1.2px;

    color: #ffffff;

    &:-webkit-autofill {
      box-shadow: inset 0 0 0 50px #181822;
      -webkit-text-fill-color: white;
    }

    &::placeholder {
      color: #ffffff2d;
    }
  }

  @media (max-width: 600px) {
    & > label {
      font-size: 17px;
      font-weight: 700;
      letter-spacing: 0.1px;
    }
  }
}
.focused {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  display: flex;
  align-items: center;

  color: #ffffff !important;
  margin-left: 10px;
}
.marginOff {
  margin: 0;
}
