.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  width: 100%;
}
.title {
  display: flex;
  width: 100%;
  justify-content: flex-start;

  font-family: "Gilroy";
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;

  color: #ffffff;
}
