.tabs {
  display: flex;
  background: #2e3038;
  border-radius: 5px;
  padding: 2px;
  height: 37px;
  width: fit-content;
}
.button {
  background: transparent;

  font-family: "Gilroy";
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;

  color: #ffffff;

  text-transform: none;
  &:hover {
    background-color: #585c6c;
  }
}
.active {
  background-color: #181822;
  &:hover,
  &:active,
  &:focus {
    background-color: #181822 !important;
  }
}
